* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: Alkatra, cursive;
  font-weight: 400;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
}

.search-form {
  color: #2e2e2e;
  background-color: #38d2d2;
  background-image: radial-gradient(93% 87% at 87% 89%, rgba(0, 0, 0, .23) 0%, rgba(0, 0, 0, 0) 86.18%), radial-gradient(66% 66% at 26% 20%, rgba(255, 255, 255, .55) 0%, rgba(255, 255, 255, 0) 69.79%, rgba(255, 255, 255, 0) 100%);
  justify-content: center;
  margin-bottom: 20px;
  padding: 20px 0;
  display: flex;
}

.search-form > input {
  height: 36px;
  color: #2e2e2e;
  border: 3px solid #007b8b;
  border-right: none;
  border-radius: 5px 0 0 5px;
  outline: none;
  padding: 5px;
}

.search-form > input:hover {
  color: #111;
}

.search-form > button {
  height: 36px;
  text-align: center;
  color: #fcfafa;
  cursor: pointer;
  background: #007b8b;
  border: 1px solid #007b8b;
  border-radius: 0 5px 5px 0;
  padding: 0 10px;
  font-family: Alkatra, cursive;
  font-size: 20px;
}

.info {
  color: #2e2e2e;
  justify-content: space-between;
  padding: 5px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  margin: 0;
  display: flex;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1400px;
  flex-flow: wrap;
  justify-content: center;
  gap: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  display: flex;
}

.photo-card {
  border-radius: 3px;
  position: relative;
}

.gallery__image {
  height: 13vw;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
  display: block;
}

.gallery__link {
  width: calc(100% / 4 - 20px);
  display: block;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

body.hidden-scroll {
  overflow: hidden;
}

.sl-overlay {
  z-index: 1035;
  background: #fff;
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.sl-wrapper {
  z-index: 1040;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.sl-wrapper * {
  box-sizing: border-box;
}

.sl-wrapper button {
  cursor: pointer;
  background: none;
  border: 0;
  padding: 0;
  font-size: 28px;
}

.sl-wrapper button:hover {
  opacity: .7;
}

.sl-wrapper .sl-close {
  z-index: 10060;
  height: 44px;
  width: 44px;
  color: #000;
  margin-top: -14px;
  margin-right: -14px;
  font-family: Arial, Baskerville, monospace;
  font-size: 3rem;
  line-height: 44px;
  display: none;
  position: fixed;
  top: 30px;
  right: 30px;
}

.sl-wrapper .sl-counter {
  z-index: 10060;
  color: #000;
  font-size: 1rem;
  display: none;
  position: fixed;
  top: 30px;
  left: 30px;
}

.sl-wrapper .sl-download {
  width: 100%;
  text-align: center;
  z-index: 10060;
  color: #fff;
  font-size: 1rem;
  display: none;
  position: fixed;
  bottom: 5px;
}

.sl-wrapper .sl-download a {
  color: #fff;
}

.sl-wrapper .sl-navigation {
  width: 100%;
  display: none;
}

.sl-wrapper .sl-navigation button {
  height: 44px;
  width: 22px;
  text-align: center;
  z-index: 10060;
  color: #000;
  margin-top: -22px;
  font-family: Arial, Baskerville, monospace;
  line-height: 44px;
  display: block;
  position: fixed;
  top: 50%;
}

.sl-wrapper .sl-navigation button.sl-next {
  font-size: 2rem;
  right: 5px;
}

.sl-wrapper .sl-navigation button.sl-prev {
  font-size: 2rem;
  left: 5px;
}

@media (min-width: 35.5em) {
  .sl-wrapper .sl-navigation button {
    width: 44px;
  }

  .sl-wrapper .sl-navigation button.sl-next {
    font-size: 3rem;
    right: 10px;
  }

  .sl-wrapper .sl-navigation button.sl-prev {
    font-size: 3rem;
    left: 10px;
  }
}

@media (min-width: 50em) {
  .sl-wrapper .sl-navigation button {
    width: 44px;
  }

  .sl-wrapper .sl-navigation button.sl-next {
    font-size: 3rem;
    right: 20px;
  }

  .sl-wrapper .sl-navigation button.sl-prev {
    font-size: 3rem;
    left: 20px;
  }
}

.sl-wrapper.sl-dir-rtl .sl-navigation {
  direction: ltr;
}

.sl-wrapper .sl-image {
  -ms-touch-action: none;
  touch-action: none;
  z-index: 10000;
  position: fixed;
}

.sl-wrapper .sl-image img {
  width: 100%;
  height: auto;
  border: 0;
  margin: 0;
  padding: 0;
  display: block;
}

@media (min-width: 35.5em) {
  .sl-wrapper .sl-image img {
    border: 0;
  }
}

@media (min-width: 50em) {
  .sl-wrapper .sl-image img {
    border: 0;
  }
}

.sl-wrapper .sl-image iframe {
  background: #000;
  border: 0;
}

@media (min-width: 35.5em) {
  .sl-wrapper .sl-image iframe {
    border: 0;
  }
}

@media (min-width: 50em) {
  .sl-wrapper .sl-image iframe {
    border: 0;
  }
}

.sl-wrapper .sl-image .sl-caption {
  color: #fff;
  background: rgba(0, 0, 0, .8);
  padding: 10px;
  font-size: 1rem;
  display: none;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.sl-wrapper .sl-image .sl-caption.pos-top {
  top: 0;
  bottom: auto;
}

.sl-wrapper .sl-image .sl-caption.pos-outside {
  bottom: auto;
}

.sl-spinner {
  height: 40px;
  opacity: 0;
  width: 40px;
  z-index: 1007;
  -ms-animation: pulsate 1s ease-out infinite;
  border: 5px solid #333;
  border-radius: 40px;
  margin: -20px 0 0 -20px;
  animation: pulsate 1s ease-out infinite;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
}

.sl-scrollbar-measure {
  width: 50px;
  height: 50px;
  position: absolute;
  top: -9999px;
  overflow: scroll;
}

.sl-transition {
  transition: transform .2s;
}

@keyframes pulsate {
  0% {
    opacity: 0;
    transform: scale(.1);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: scale(1.2);
  }
}

